import React from 'react';
import classes from './css/catalog.module.css'
import cap from  './img/cap.png'
import capDown from  './img/capDown.png'
import capUp from  './img/capUp.png'
import circle from  './img/circle.png'
import redCircle from  './img/redCircle.png'
import circleFull from  './img/circleFull.png'
import left from  './img/left.png'
import bottom from  './img/bottom.png'
import right from  './img/right.png'
import {Link} from "react-router-dom";

const Catalog1 = () => {
    return (

        <div className={classes.container}>
            <div className={classes.mainBlock}>
                <div className={classes.textBlock}>
                    <h2 className={classes.newEra}>NEW ERA</h2>
                    <p className={classes.text}>
                      2021    <br/>
                     ,   70, <br/>
                       </p>
                    <p className={classes.textTwo}>
                          2021    
                          ...
                    </p>
                    <Link to="/catalog/"><button className={classes.button}> </button></Link>
                </div>
                <div className={classes.images}>
                    <img className={classes.cap} src={cap} alt=""/>
                    <img className={classes.capUp} src={capUp} alt=""/>
                    <img className={classes.capDown} src={capDown} alt=""/>
                    <img className={classes.circle} src={circle} alt=""/>
                    <img className={classes.circleFull} src={circleFull} alt=""/>
                    <img className={classes.redCircle} src={redCircle} alt=""/>
                    <img className={classes.capLeft} src={left} alt=""/>
                    <img className={classes.capRight} src={right} alt=""/>
                    <img className={classes.capBottom} src={bottom} alt=""/>
                </div>
            </div>
        </div>
    );
};

export default Catalog1;